<template>
  <div class="tab-control">
    <div  v-for="(item,index) in titles"
          class="tab-control-item"
          :class="{active: index === countindex}"
          @click="itemclick(index)">
      <span>{{item}}</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "TabControl",
    data(){
      return{
        countindex:0,
      }
    },
    props:{
      titles:{
        type:Array,
        default(){
          return []
        }
      }
    },
    methods:{
      itemclick(index){
        this.countindex = index;
        this.$emit('tabclick',index)
      }
    }

  }
</script>

<style scoped lang="scss">
  .tab-control{
    display: flex;
    text-align: center;
    height: 40px;
    line-height: 40px;
    .tab-control-item{
      flex: 1;
      span{

      }
    }
  }
  .active{
    color: #ff8198;
    span{
      border-bottom: 3px solid #ff8198;
      padding: 5px;
    }
  }
</style>